import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';

import ModalOpSlct from "./ModalOpSlct"
import { TextSrs } from './Text';


const WeArBdToTxt = { Wepn: '', Armr: '', Bddy: 'Buddy' }



export default function DataGridEq(props) {
  const { data, C, Calc, list, Op, height } = props.props

  const columns = [
    // { field: 'id', headerName: 'ID', width: 90 },
    {
      field: 'Ctg',
      headerName: '',
      width: 50,
      disableColumnMenu: true,//

    },
    {
      field: 'name',
      headerName: '',
      width: 200,
      disableColumnMenu: true,//

    },
    {
      field: 'HP',
      headerName: 'HP',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//
    },
    {
      field: 'TP',
      headerName: 'TP',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//
    },
    {
      field: '',
      headerName: '',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//
    },
    {
      field: '',
      headerName: '',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//
    },
    {
      field: '',
      headerName: '',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//
    },
    {
      field: '',
      headerName: '',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//
    },
    // {
    //   field: '',
    //   headerName: '',
    //   align: 'right',
    //   minWidth: 49,
    //   width: 49,
    //   disableColumnMenu: true,//
    // },
    {
      field: 'UqOp',
      headerName: '',
      width: 250,
    },
    {
      field: 'Op',
      headerName: '(x3)',
      width: 250,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          OpB={params.row.Op}
          data={data}
          Eq={params.row.Eq}
          Calc={Calc}
        />
      )
    },
    {
      field: 'Srs',
      headerName: '',
      width: 400,
    },

  ];


  const rows = [];
  Object.keys(WeArBdToTxt).forEach((Eq) => {
    Object.keys(data[Eq]).forEach((name, i) => {
      rows.push({
        ...data[Eq][name].St,
        id: name,
        Eq: Eq,
        Ctg: WeArBdToTxt[Eq],
        name: name,
        UqOp: data[Eq][name].UqOp,
        Op: data[Eq][name].Op,
        Srs: TextSrs({ name: name, data: data, Eq: Eq, }),
      })
    })
  })




  return (
    // <Box sx={{ height:height, width: '100%' }}>
    <DataGrid
      rows={rows}
      columns={columns}
      experimentalFeatures={{ newEditingApi: true }}
      density='compact'//
      autoHeight//
      components={{
        Toolbar: GridToolbar,// 
      }}
      // hideFooter//
      // hideFooterSelectedRowCount//
      // hideFooterPagination//(
      //   initialState={{
      //   pagination: {
      //     paginationModel: {
      //       pageSize: 10,
      //     },
      //   },
      // }}
      pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      getRowHeight={() => 'auto'}

    />
    // </Box>
  );
}