        //         !isIncludes(data.list.Gr, E) ||// 
        //         isIncludes(data.list.Gr, E) && E.includes(Gr)//
        //       ) {
        //         console.log(N)
        //         data.Skl[N].Eff.map((EffEl) => {
        //           if (E === EffEl.E) {//data.Skl[N].EffEffEl

        //             let AE2 = AE
        //             if (EffEl.Tgt === 'Enmy') {
        //               AE2 = EA[AE]
        //             }

        //             if (
