import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';


const columns = [
  // {
  //   field: 'No',
  //   headerName: '',
  //   width: 50,
  // },

  {
    field: 'St',
    headerName: '',
    width: 100,
    sortable: false,//false:
    disableColumnMenu: true,//
  },
  {
    field: 'Bas',
    headerName: '',
    width: 100,
    sortable: false,//false:
    disableColumnMenu: true,//
  },
  {
    field: 'Org',
    headerName: '',
    width: 100,
    sortable: false,//false:
    disableColumnMenu: true,//
  },
  {
    field: 'Btl',
    headerName: '',
    width: 100,
    sortable: false,//false:
    disableColumnMenu: true,//
  },
];


export default function DataGridSt(props) {
  const { data, Calc, list, cNameF, AE } = props.props

  const rows = data.HTADSIM.map((S, i) => {
    return ({
      id: i,
      St: S,
      Bas: data.C1[cNameF][AE].St.Bas[S],
      Org: data.C1[cNameF][AE].St.Org[S],
      Btl: data.C1[cNameF][AE].St.Btl[S],
    })
  })





  return (
    <Box sx={{ width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//
        autoHeight//
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//(
        getRowHeight={() => 'auto'}
      />
    </Box>
  );
}